.square {
    background: #fff;
    border: 2px solid black;
    float: left;
    font-size: 3rem;
    font-weight: bold;
    height: 5rem;
    width: 5rem;
    padding: 0;
    text-align: center;
    border-radius: 0px;
}

.square.X {
    background-color: #ff5c7d;
}

.square.O {
    background-color: #a0e4a5;
}

.square.winner {
    filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.613));
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
    font-size: 2rem;
}

.game {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .game {
        flex-direction: column;
        align-items: center;
    }
}

.game-info {
    margin-top: 0.5rem;
    margin-left: 1.5rem;
}

.game-info li {
    margin: 0.5rem;
    transition: all 200ms;
}

button {
    background-color: rgba(51, 51, 51, 0.05);
    border-radius: 8px;
    border-width: 0;
    color: #333333;
    cursor: pointer;
    font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial,
        sans-serif;
    font-size: 14px;
    line-height: 20px;
    list-style: none;
    margin: 0;
    padding: 10px 12px;
    transition: all 200ms;
    white-space: nowrap;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

li {
    background-color: rgba(51, 51, 51, 0.05);
    border-radius: 8px;
    border-width: 0;
    color: #333333;
    margin: 0;
    padding: 10px 12px;
    white-space: nowrap;
}

.current {
    background-color: rgba(1, 108, 238, 0.137);
}

table {
    margin: 0.5rem;
}

td {
    border: 1px solid;
    height: 2rem;
    width: 2rem;
    text-align: center;
}

button.history-table {
    margin-left: 0.5rem;
    font-size: 2rem;
    background-color: transparent;
}
